/* Variables
/* ---------------------------------------------------------- */

:root {
  /*
 * Colors
 * ========================================================================== */
  --palette-white-100: #fff;
  --palette-white-200: #fafafa;
  --palette-white-300: #f0f2f3;
  --palette-white-400: #f3f3f3;
  --palette-gray-100: #eee;
  --palette-gray-200: #e3e9ed;
  --palette-gray-300: #aaa;
  --palette-gray-400: #666;
  --palette-black-100: #000;
  --palette-blue-100: #295be6;

  --RGB-black-100: 0, 0, 0;

  /*
 * Typography
 * ========================================================================== */
  --font-regular: 'Helvetica Neue', Arial, Roboto, sans-serif;

  --typesize-x-small: 14px;
  --typesize-small: 16px;
  --typesize-base: 18px;
  --typesize-large: 24px;
  --typesize-x-large: 32px;
  --typesize-xx-large: 40px;
  --typesize-xxx-large: 80px;

  /*
 * Layout
 * ========================================================================== */
  --width-xx-small: 740px;
  --width-x-small: 800px;
  --width-small: 960px;
  --width-base: 1024px;

  /*
 * Post
 * ========================================================================== */
  --post-base-rounding: 8px;

  /*
 * Badge
 * ========================================================================== */
  --badge-base-rounding: 6px;

  /*
 * Input
 * ========================================================================== */
  --input-base-rounding: 6px;

  /*
 * Buttons
 * ========================================================================== */
  --button-base-rounding: 6px;

  /*
 * Code
 * ========================================================================== */
  --code-base-rounding: 3px;
}

/* Reset
/* ---------------------------------------------------------- */
* {
  box-sizing: border-box;
}

*:focus {
  outline: 0;
}

h1,
h2,
h3,
h4,
h5 {
  font-weight: normal;
  margin: 0;
}

ul,
ol {
  padding: 0;
  margin: 0;
}

li {
  list-style: none;
}

img {
  display: block;
  max-width: 100%;
}

body,
figure,
pre,
pre[class*='language-'],
blockquote,
p {
  margin: 0;
}

a {
  text-decoration: none;
  color: inherit;
}

button {
  background: transparent;
  border: none;
  cursor: pointer;
}

input,
textarea {
  font-size: inherit;
  width: 100%;
  border: none;
}

input[type='submit'] {
  width: initial;
  cursor: pointer;
}

/* ==========================================================================
  Base styles: opinionated defaults
========================================================================== */

h1 {
  font-size: var(--typesize-xxx-large);
  line-height: 1.19;
}

h2 {
  font-size: var(--typesize-x-large);
  line-height: 1.2;
}

input,
textarea {
  color: var(--palette-black-100);
  background: var(--palette-white-100);
  padding: 11px 16px;
  box-shadow: 0px 2px 6px rgba(0, 0, 0, 0.16);
  border-radius: var(--input-base-rounding);
}

input[type='submit'] {
  background: var(--palette-black-100);
  color: var(--palette-white-100);
}

blockquote {
  font-size: var(--typesize-large);
  line-height: 1.42;
}

form {
  font-size: var(--typesize-small);
  line-height: 1.19;
}
